<template>
    <div id="single-blog">
        <h1> {{ blog.title }} </h1>
        <article> {{ blog.body }} </article>
        <p>作者：{{ blog.author }}</p>
        <p class="fenlei">分类： 
            <ul>
                <li v-for="category in blog.categories"> {{category}} </li>
            </ul>
        </p>
        <div class="con"> {{blog.content}} </div>
        <button @click="deleteSingleBlog">删除</button>
        <router-link :to="'/edit/' + id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name:"single-blog",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        //注意这里的字符串拼接
        // this.$http.get('https://my-blog-0001.firebaseio.com/posts/' + this.id + ".json")
        axios.get('/posts/' + this.id + ".json")
            .then( data =>{
                this.blog = data.data;
            });
    },
    methods:{
        deleteSingleBlog(){
            // this.$http.delete("https://my-blog-0001.firebaseio.com/posts/" + this.id + ".json")
            axios.get('/posts/' + this.id + ".json")
                .then(response=>{
                    //编程式写法
                    this.$router.push({path:"/"})
                })
        }
    }
}
</script>

<style>
    #single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: rgb(102, 119, 204);
		border: 1px dotted #aaa;
	}
    #single-blog a{
        color:#ff5000;
    }
    #single-blog .fenlei{
        display: block;
    }
    #single-blog .fenlei ul{
        display: inline-block;
        padding-left:0px;
    }
    #single-blog .fenlei ul li{
        float: left;
        margin-left: 0px;
    }
    .con{
        display: block;
        width: 100%;
        height: 200px;
        border:1px solid #fff;
        margin-bottom: 10px;
        background: #fff;
    }
</style>




